import React from "react";
import PropTypes from "prop-types";
import "../styles/info_panel.css";

export default class IconInfoPanel extends React.Component{
    render(){
        let instance = this;
        if(this.props.icons !== null && this.props.values !== null && this.props.icons.length !== this.props.values.length){
            throw new Error("icons and values must be same length");
        }
        return <div className="info-panel">
            {
                this.props.icons.map(function(icon, i) {
                    return [icon, instance.props.values[i], instance.props.links[i]];
                  }).map((v, i)=>{
                      let icon = v[0];
                      let value = v[1];
                      return <div className="info-item2" onClick={()=>{
                          this.props.listener(i);

                      }}>
                                <span className="info-icon">
                                    {icon}
                                </span>
                                <a href={v[2]} style={{
                                    textDecoration: "none",
                                }}>
                                    <span className="info-value" >
                                        {value}
                                    </span>
                                </a>
                             </div>
                  })
            }
        </div>
    }
}
IconInfoPanel.propTypes = {
    icons: PropTypes.arrayOf(PropTypes.any),
    values: PropTypes.arrayOf(PropTypes.string),
    listener: PropTypes.func
};